*{
    margin:0;
    padding:0;
    text-decoration: none;

}
.container2{
    display:flex;
    background-image: url(./images/bg-img.jpg);
    background-size: cover;
}
main{
    width: 100%;
    padding: 20px;
}
.sidebar{
    background: rgba(110, 110, 112, 0.502);
    color: #fff;
    height: 100vh;
    width: 200px;
    transition: all 0.5s;
}
.top_section{
    display: flex;
    align-items:center;
    padding:20px 15px;
}
.logo{
    font-size: 30px;
}
.bars{
    display: flex;
    font-size: 25px;
    margin-left: 50px;
}
.link{
    display: flex;
    color: #fff;
    padding: 10px 15px;
    gap: 15px;
    transition: all 0.5s;
    text-decoration: none;
}
.link:hover{
    background: yellow;
    color: #000;
    transition: all 0.5s;
}
.active{
    background: yellow;
    color: #000;
}
.icon, .link_text{
    font-size: 20px;
}

.box{
    margin: 50px;
    width: 100%;
    height: 80vh;
    border: solid;
    border-color: yellow;
    border-radius: 0%;
    border-width: 7px;
    margin-left: 0%;
    background-color: rgba(0, 0, 0, 0.379);
    color: #fff;
  }
  .flex{
    display: float;
    
  }
  .card{
    float: left;
    background-color: transparent;
    padding: 2px;
    margin-top: 20px;
    height: 400px;
    
  }
  .card2{
    height: 80%;
    width: 80%;
    background-color: rgba(13, 125, 104, 0.685);
    border-radius: 3%;
    margin: auto;
    padding: 5%;
  }
  .highlight{
    width: 100%;
    background-color: rgba(5, 63, 48, 0.879);
    padding: 3%;
    border-radius: 3%;
    margin-top: 20%;
  }
  .card2 button{
    justify-content: center;
    margin-top: 20%;
  }